import { useState } from "react";
import TextField from "@mui/material/TextField";
import { styled } from "@mui/material/styles";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import TextareaAutosize from "@mui/base/TextareaAutosize";
import Box from "@mui/material/Box";
//import Button from "@mui/material/Button";

export default function Popup(props) {
  //const primaryCOl = "#35b3a8";
  const CssTextField = styled(TextField)({
    "& label.Mui-focused": {
      color: "#000",
      borderColor: "#35b3a8",
      fontSize: "24px",
      marginBottom: "20px"
    },
    ".MuiInput-root.MuiInput-underline.MuiInputBase-root:before": {
      borderBottom: "2px solid #35b3a8"
    },
    ".MuiInput-root.MuiInput-underline.MuiInputBase-root:after": {
      border: "2px solid #35b3a8"
    },
    ".MuiInput-root.MuiInput-underline.MuiInputBase-root .MuiInput-input.MuiInputBase-input": {
      fontSize: "12px",
      marginTop: "20px"
    }
  });
  const [heardUs, setheardUs] = useState("");
  const handleChange = (event) => {
    setheardUs(event.target.value);
  };
  return (
    <div>
      <div className="bg-configurator-box-background">
        <div className="container">
          <div className="row">
            <div className="packaging-summary-col">
              <div>
                <h5 className="text-footer-gray">Packaging Summary</h5>
              </div>
            </div>
            <div className="packaging-summary-col"></div>
            <div className="packaging-summary-col"></div>
          </div>
          <div className="row">
            <div className="packaging-summary-col">
              <div className="summary-title">
                <p>
                  STYLE{" "}
                  <span className="summary-details" id="styleformdata">
                    None
                  </span>
                </p>
                <p>
                  DIMENSIONS{" "}
                  <span className="summary-details" id="dimensionformdata">
                    L:0, W:0, H:0
                  </span>
                </p>
                <p>
                  MATERIALS{" "}
                  <span className="summary-details" id="materialformdata">
                    None
                  </span>
                </p>
              </div>
            </div>
            <div className="packaging-summary-col">
              <div className="summary-title">
                <p>
                  PRINT SURFACE{" "}
                  <span className="summary-details" id="printsurfaceformdata">
                    None
                  </span>
                </p>
                <p>
                  PRINT SPEC{" "}
                  <span className="summary-details" id="printspecformdata">
                    None
                  </span>
                </p>
                <p>
                  COATING{" "}
                  <span className="summary-details" id="coatingformdata">
                    None
                  </span>
                </p>
              </div>
            </div>
            <div className="packaging-summary-col">
              <div className="summary-title">
                <p>
                  FINISHING{" "}
                  <span className="summary-details" id="finishingformdata">
                    None
                  </span>
                </p>
                <p>
                  QUANTITY{" "}
                  <span className="summary-details" id="quantityformdata">
                    0
                  </span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="FormBody">
        <h2
          style={{ textAlign: "left", color: "#35b3a8" }}
          className="FormTitle"
        >
          Submit Packaging Breif
        </h2>
        <form>
          <CssTextField
            required
            variant="standard"
            label="Name"
            id="custom-css-outlined-input"
            placeholder="Enter Name"
          />
          <CssTextField
            required
            variant="standard"
            label="Date Required"
            id="custom-css-outlined-input"
            placeholder="Enter Date (DD/MM/YYYY)"
          />
          <CssTextField
            required
            variant="standard"
            label="Email"
            id="custom-css-outlined-input"
            placeholder="Enter e-mail"
          />
          <CssTextField
            required
            variant="standard"
            label="Telephone Number"
            id="custom-css-outlined-input"
            placeholder="Enter Phone Number"
          />
          <Box
            className="leftBox FormBox"
            component="span"
            sx={{
              width: "48%",
              marginRight: "1%",
              marginLeft: "1%"
            }}
          >
            <FormControl
              variant="standard"
              style={{
                textAlign: "left",
                color: "#35b3a8",
                borderColor: "#35b3a8"
              }}
              sx={{ m: 1, minWidth: "100%" }}
              //focused
            >
              <InputLabel
                //style={{ color: "#35b3a8" }}
                id="demo-simple-select-standard-label"
              >
                Where Did You Her About Us
              </InputLabel>
              <Select
                focused
                //displayEmpty
                labelId="demo-simple-select-standard-label"
                id="demo-simple-select-standard"
                value={heardUs}
                onChange={handleChange}
                label="Where Did You Her About Us"
                Style={{ color: "#35b3a8", borderColor: "#35b3a8" }}
              >
                <MenuItem value="">
                  <em>Choose an option</em>
                </MenuItem>
                <MenuItem value={"Google"}>Google Search</MenuItem>
                <MenuItem value={"Referral"}>Referral</MenuItem>
                <MenuItem value={"Customer"}>Customer</MenuItem>
                <MenuItem value={"LinkedIn"}>LinkedIn</MenuItem>
                <MenuItem value={"Instagram"}>Instagram</MenuItem>
                <MenuItem value={"Other"}>Other</MenuItem>
              </Select>
            </FormControl>
            <CssTextField
              required
              variant="standard"
              label="Project Title"
              id="custom-css-outlined-input"
              placeholder="Enter Project"
            />
            <CssTextField
              required
              variant="standard"
              label="Comapany"
              id="custom-css-outlined-input"
              placeholder="Enter Comapany"
            />
          </Box>
          <Box
            component="span"
            sx={{
              width: "48%"
              //marginLeft: "1%"
            }}
            className="RightBox FormBox"
          >
            <InputLabel
              style={{ textAlign: "left" }}
              id="demo-simple-select-standard-label"
            >
              Any Other Requirements
            </InputLabel>
            <TextareaAutosize
              minRows={12}
              aria-label="empty textarea"
              placeholder="Enter Your Requirement (Version, packing, Shipping, etc.)"
              style={{
                marginTop: "10px",
                width: "96%",
                padding: "15px",
                borderWidth: "2px",
                borderColor: "#35b3a8"
              }}
            />
          </Box>
        </form>
      </div>
    </div>
  );
}
