import React, { useState } from "react";

export default function TabInputs(props) {
  const [checked, setChecked] = useState(0);
  if (props.val === props.selected) {
    setChecked(1);
  }
  return (
    <label className="InputOuter">
      <input
        type={props.type}
        name={props.name}
        value={props.val}
        checked={checked}
        onChange={() => {
          props.onclick(props.name, props.val);
        }}
      />
      <img src={props.img} alt="" />
      <span>{props.attributeLabel}</span>
    </label>
  );
}
